/**
 * @property openId openid
 * @property mobile 
 * @property captcha 
 * @property clientId idbigapp
 * @property source 
 * @property niceName 
 * @property avatarUrl 
 * @property sex 
 */
interface RegisterRequestInfo {
  openId: string
  mobile: string
  captcha: string
  clientId: string
  source: number
  niceName?: string
  avatarUrl?: string
  sex?: number
}
/**
 * 
 */
export const register = (data: RegisterRequestInfo) => {
  return request({
    url: '/api/v1/thirdparty/wechat/miniprogram/register',
    method: 'POST',
    domain: 'UAA',
    data
  })
}

