import dayjs from '$dayjs'

/**
 * 
 * @param time
 * @param formatter 
 * @returns YYYY.MM.DD
 */
export const formatDate = (time: string | number, formatter?: string): string => {
  return time ? dayjs(time).format(formatter || 'YYYY.MM.DD') : ''
}

/**
 * 
 * @param dateTime
 * @returns
 */
export function formatTime(dateTime: string) {
  const timestamp = new Date(dateTime.replace(/-/g, '/')).getTime() //  IOS 
  const now = new Date().getTime()
  const timer = (now - timestamp) / 1000
  let tip = ''

  if (timer <= 0) {
    tip = ''
  } else if (Math.floor(timer / 60) <= 0) {
    tip = ''
  } else if (Math.floor(timer) < 3600) {
    tip = Math.floor(timer / 60) + ''
  } else if (timer >= 3600 && timer < 86400) {
    tip = Math.floor(timer / 3600) + ''
  } else if (timer / 86400 < 8) {
    tip = Math.floor(timer / 86400) + ''
  } else {
    tip = dateTime
  }
  return tip
}

/**
 * 
 * @param dateTime
 * @returns
 */
export function formatCountDownTime(dateTime: string) {
  const timestamp = new Date(dateTime.replace(/-/g, '/')).getTime() //  IOS 
  const now = new Date().getTime()
  const timer = (timestamp - now) / 1000
  let tip = ''

  if (Math.floor(timer) < 0) {
    tip = ''
  } else if (timer > 86400) {
    tip = '' + (timer % 86400 === 0 ? timer / 86400 : Math.floor(timer / 86400) + 1) + ''
  } else if (timer >= 3600 && timer < 86400) {
    tip = '' + (timer % 3600 === 0 ? timer / 3600 : Math.floor(timer / 3600) + 1) + ''
  } else if (Math.floor(timer) < 3600) {
    tip = '' + (timer % 60 === 0 ? timer / 60 : Math.floor(timer / 60) + 1) + ''
  }
  return tip
}
