/**
 * @description 
 * 
 * @param {object | string} customStyle 
 * @param {String} target object-string-
 * @returns {object|string}
 */
export const addStyle = function (customStyle, target = 'object') {
  // 
  if (test.empty(customStyle) || (typeof customStyle === 'object' && target === 'object') || (target === 'string' && typeof customStyle === 'string')) {
    return customStyle
  }
  // 
  if (target === 'object') {
    // (padding: 20px 0)
    customStyle = trim(customStyle)
    // ";"
    const styleArray = customStyle.split(';')
    const style = {}
    // 
    for (let i = 0; i < styleArray.length; i++) {
      // 'font-size:20px;color:red;'";"styleArray
      if (styleArray[i]) {
        const item = styleArray[i].split(':')
        style[trim(item[0])] = trim(item[1])
      }
    }
    return style
  }
  // 
  let string = ''
  for (const i in customStyle) {
    // css
    const key = i.replace(/([A-Z])/g, '-$1').toLowerCase()
    string += `${key}:${customStyle[i]};`
  }
  // 
  return trim(string)
}

/**
 * @description rpxupx%pxautopx
 * @param {string|number} value 
 * @param {string} unit  px
 */
export const addUnit = function (value = 'auto', unit = uni?.$u?.config?.unit ?? 'px') {
  value = String(value)
  // uViewnumber
  return test.number(value) ? `${value}${unit}` : value
}

/**
 * @description 
 * @param {object} obj 
 * @returns {*} 
 */
export const deepClone = function (obj) {
  // 
  if ([null, undefined, NaN, false].includes(obj)) {
    return obj
  }
  if (typeof obj !== 'object' && typeof obj !== 'function') {
    // 
    return obj
  }
  const o = test.array(obj) ? [] : {}
  for (const i in obj) {
    if (obj.hasOwnProperty(i)) {
      o[i] = typeof obj[i] === 'object' ? deepClone(obj[i]) : obj[i]
    }
  }
  return o
}

/**
 * @description JS
 * @param {object} target 
 * @param {object} source 
 * @returns {object|boolean} false
 */
export const deepMerge = function (target = {}, source = {}) {
  target = deepClone(target)
  if (typeof target !== 'object' || typeof source !== 'object') {
    return false
  }
  for (const prop in source) {
    if (!source.hasOwnProperty(prop)) {
      continue
    }
    if (prop in target) {
      if (typeof target[prop] !== 'object') {
        target[prop] = source[prop]
      } else if (typeof source[prop] !== 'object') {
        target[prop] = source[prop]
      } else if (target[prop].concat && source[prop].concat) {
        target[prop] = target[prop].concat(source[prop])
      } else {
        target[prop] = deepMerge(target[prop], source[prop])
      }
    } else {
      target[prop] = source[prop]
    }
  }
  return target
}

/**
 * @description px  "xxpx""xxrpx""xxxrpx"uni.upx2px
 * @param {number|string} value px
 * @param {boolean} unit
 * @returns {number|string}
 */
export const getPx = function (value, unit = false) {
  if (test.number(value)) {
    return unit ? `${value}px` : Number(value)
  }
  // rpxpx
  if (/(rpx|upx)$/.test(value)) {
    return unit ? `${uni.upx2px(parseInt(value))}px` : Number(uni.upx2px(parseInt(value)))
  }
  return unit ? `${parseInt(value)}px` : parseInt(value)
}

/**
 * @description 
 * @link  https://uniapp.dcloud.io/api/system/info?id=getsysteminfosync
 */
export const sys = function () {
  return uni.getSystemInfoSync()
}

/**
 * @description  : await sleep(20)20ms
 * @param {number} value  ms 
 * @returns {Promise} promise
 */
export const sleep = function (value = 30) {
  return new Promise((resolve) => {
    setTimeout(() => {
      resolve()
    }, value)
  })
}
