// #ifdef H5
const u = navigator.userAgent
// ios
export const isIos = !!u.match(/(i[^;]+;( U;)? CPU.+Mac OS X)/)
// android
export const isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1
// 
export const isInWechat = u.toLowerCase().indexOf('micromessenger') > -1
