import { defineStore } from 'pinia'
import { getPointRatio } from '@src/api/goods'

export const useGoodsStore = defineStore({
  id: 'goods',
  state: () => {
    return {
      pointRatio: 1
    }
  },
  actions: {
    /**
     *  1 = ${pointRatio}
     */
    async getPointRatio() {
      const res = await getPointRatio()
      this.pointRatio = res
      return res
    }
  }
})
