import { request } from './request'

/**
 * 
 * @param data
 * @returns
 */
export const addGoodsToCart: Cart.AddCart = (data) => {
  return request({
    url: '/web/cart-items',
    method: 'POST',
    domain: 'MALL',
    data
  })
}

/**
 * 
 * @param data
 * @returns
 */
export const getCartGoodsNumber = () => {
  return request({
    url: '/web/cart-items/user/count',
    method: 'GET',
    domain: 'MALL'
  })
}

/**
 * 
 * @returns
 */
export const getCartList = () => {
  return request({
    url: '/web/cart-items/user',
    method: 'GET',
    domain: 'MALL'
  })
}

/**
 * 
 * @param ids
 * @returns
 */
export const removeGoods = (ids: string) => {
  return request({
    url: `/web/cart-items/batch?ids=${ids}`,
    method: 'DELETE',
    domain: 'MALL'
  })
}

/**
 * sku
 * @param data
 * @returns
 */
export const changeGoodsInfo = (data: AnyObject) => {
  return request({
    url: '/web/cart-items',
    method: 'PUT',
    domain: 'MALL',
    data
  })
}
/**
 * 
 * @param data
 * @returns
 */
export const changeGoodsNumber = (data: AnyObject) => {
  return request({
    url: '/web/cart-items/quantity',
    method: 'PUT',
    domain: 'MALL',
    data
  })
}
