import { request, uploadRequest } from './request'

// 
export const getOrderList: AfterSale.GetOrderList = (data) => {
  return request({
    url: '/web/after-sales',
    method: 'GET',
    domain: 'MALL',
    data
  })
}

// 
export const setAfterRefundCancel: AfterSale.Cancel = (afterSaleId) => {
  return request({
    url: `/web/after-sales/${afterSaleId}/cancel`,
    method: 'PUT',
    domain: 'MALL'
  })
}

