<template>
  <view class="u-tabs">
    <view class="u-tabs__wrapper">
      <slot name="left" />
      <view class="u-tabs__wrapper__scroll-view-wrapper">
        <scroll-view :scroll-x="scrollable" :scroll-left="scrollLeft" :show-scrollbar="false" scroll-with-animation
          class="u-tabs__wrapper__scroll-view" ref="u-tabs__wrapper__scroll-view">
          <view class="u-tabs__wrapper__nav" ref="u-tabs__wrapper__nav">
            <!-- tabs -->
            <view class="u-tabs__wrapper__nav__item" v-for="(item, index) in list" :key="index"
              @tap="clickHandler(item, index)" :ref="`u-tabs__wrapper__nav__item-${index}`"
              :style="[addStyle_fn(itemStyle), {flex: scrollable ? '' : 1}]"
              :class="[`u-tabs__wrapper__nav__item-${index}`, item.disabled && 'u-tabs__wrapper__nav__item--disabled']">
              <text :class="[item.disabled && 'u-tabs__wrapper__nav__item__text--disabled']"
                class="u-tabs__wrapper__nav__item__text" :style="[textStyle(index)]">{{ item[keyName] }}</text>
              <!-- <u-badge
								:show="!!(item.badge && (item.badge.show || item.badge.isDot || item.badge.value))"
								:isDot="item.badge && item.badge.isDot || propsBadge.isDot"
								:value="item.badge && item.badge.value || propsBadge.value"
								:max="item.badge && item.badge.max || propsBadge.max"
								:type="item.badge && item.badge.type || propsBadge.type"
								:showZero="item.badge && item.badge.showZero || propsBadge.showZero"
								:bgColor="item.badge && item.badge.bgColor || propsBadge.bgColor"
								:color="item.badge && item.badge.color || propsBadge.color"
								:shape="item.badge && item.badge.shape || propsBadge.shape"
								:numberType="item.badge && item.badge.numberType || propsBadge.numberType"
								:inverted="item.badge && item.badge.inverted || propsBadge.inverted"
								customStyle="margin-left: 4px;"
							></u-badge> -->
            </view>

            <!-- tabs -->
            <view class="u-tabs__wrapper__nav__line" ref="u-tabs__wrapper__nav__line" :style="[{
            	width: addStyle_fn(lineWidth),
            	transform: `translate(${lineOffsetLeft}px)`,
            	transitionDuration: `${firstTime ? 0 : duration}ms`,
            	height: addStyle_fn(lineHeight),
            	background: lineColor,
            	backgroundSize: lineBgSize,
            }]">

            </view>

          </view>
        </scroll-view>
      </view>
      <slot name="right" />
    </view>
    <!--  -->
    <view class="u-tabs-content" :style="[{
    	width: '100%',
    	height: '200px',
    	background: 'red',
    }]">
      <slot name="content" :current="innerCurrent" />
    </view>
  </view>
</template>

<script>
import { props, addStyle, addUnit, deepMerge, getPx, sys, sleep } from './props.js';
/**
 * Tabs 
 * @description tabs tab
 * @tutorial https://www.uviewui.com/components/tabs.html
 * @property {String | Number}	duration			 200 
 * @property {String | Number}	swierWidth			swiper '750rpx' 
 * @property {String}	keyName	 `list` 'name' 
 * @event {Function(index)} change  index: tab0
 * @event {Function(index)} click  index: tab0
 * @example <u-tabs :list="list" :is-scroll="false" :current="current" @change="change"></u-tabs>
 */
export default {
  name: 'lc-tabs',
  props: props,
  options: {
    virtualHost: true
  },
  data () {
    return {
      lineHeightH: '20px',
      addStyle_fn: addStyle,
      addStyle_fn: addUnit,
      firstTime: true,
      scrollLeft: 0,
      scrollViewWidth: 0,
      lineOffsetLeft: 0,
      tabsRect: {
        left: 0
      },
      innerCurrent: 0,
      moving: false,
    }
  },
  watch: {
    current: {
      immediate: true,
      handler (newValue, oldValue) {
        // 
        if (newValue !== this.innerCurrent) {
          this.innerCurrent = newValue
          this.$nextTick(() => {
            this.resize()
          })
        }
      }
    },
    // listlist
    list () {
      this.$nextTick(() => {
        this.resize()
      })
    }
  },
  computed: {
    textStyle () {
      return index => {
        const style = {}
        // 
        const customeStyle = index === this.innerCurrent ? addStyle(this.activeStyle) :
          addStyle(
            this.inactiveStyle)
        // nvuestyle!import
        if (this.list[ index ].disabled) {
          style.color = '#c8c9cc'
        }
        return deepMerge(customeStyle, style)
      }
    },
    // propsBadge () {
    //   return props.badge
    // },
    $u () {
      // #ifndef APP-NVUE
      // nvuepropshttpmixinsetData
      return deepMerge(uni.$u, {
        props: undefined,
        http: undefined,
        mixin: undefined
      })
      // #endif
    },
  },
  async mounted () {
    this.init()
  },
  methods: {
    setLineLeft () {
      const tabItem = this.list[ this.innerCurrent ];
      if (!tabItem) {
        return;
      }
      // 
      let lineOffsetLeft = this.list
        .slice(0, this.innerCurrent)
        .reduce((total, curr) => total + curr.rect.width, 0);
      // px
      const lineWidth = getPx(this.lineWidth);
      this.lineOffsetLeft = lineOffsetLeft + (tabItem.rect.width - lineWidth) / 2

      // tab item
      // nvuestylefalse()
      if (this.firstTime) {
        setTimeout(() => {
          this.firstTime = false
        }, 10);
      }
    },
    // 
    clickHandler (item, index) {
      // disabledclickchange
      this.$emit('click', {
        ...item,
        index
      })
      // disabled
      if (item.disabled) return
      this.innerCurrent = index
      this.resize()
      this.$emit('change', {
        ...item,
        index
      })
    },
    init () {
      sleep().then(() => {
        this.resize()
      })
    },
    setScrollLeft () {
      // tabtabwidthleft()
      const tabRect = this.list[ this.innerCurrent ]
      // item
      const offsetLeft = this.list
        .slice(0, this.innerCurrent)
        .reduce((total, curr) => {
          return total + curr.rect.width
        }, 0)
      // 
      const windowWidth = sys().windowWidth
      // tabs-itemscroll-view
      let scrollLeft = offsetLeft - (this.tabsRect.width - tabRect.rect.width) / 2 - (windowWidth - this.tabsRect
        .right) / 2 + this.tabsRect.left / 2
      // scrollLeftscroll-viewtabs
      scrollLeft = Math.min(scrollLeft, this.scrollViewWidth - this.tabsRect.width)
      this.scrollLeft = Math.max(0, scrollLeft)
    },
    // 
    resize () {
      // list
      if (this.list.length === 0) {
        return
      }
      Promise.all([ this.getTabsRect(), this.getAllItemRect() ]).then(([ tabsRect, itemRect = [] ]) => {
        this.tabsRect = tabsRect
        this.scrollViewWidth = 0
        itemRect.map((item, index) => {
          // scroll-view
          this.scrollViewWidth += item.width
          // itemX
          this.list[ index ].rect = item
        })
        // tabs
        this.setLineLeft()
        this.setScrollLeft()
      })
    },
    // 
    getTabsRect () {
      return new Promise(resolve => {
        this.queryRect('u-tabs__wrapper__scroll-view').then(size => resolve(size))
      })
    },
    // 
    getAllItemRect () {
      return new Promise(resolve => {
        const promiseAllArr = this.list.map((item, index) => this.queryRect(
          `u-tabs__wrapper__nav__item-${index}`, true))
        Promise.all(promiseAllArr).then(sizes => resolve(sizes))
      })
    },
    // 
    queryRect (el, item) {
      // $uGetRectuViewhttps://www.uviewui.com/js/getRect.html
      // this.$uGetRectgetRect
      return new Promise(resolve => {
        this.$uGetRect(`.${el}`).then(size => {
          resolve(size)
        })
      })
    },
    // 
    // bug(2020-07-21)
    // view
    $uGetRect (selector, all) {
      return new Promise((resolve) => {
        uni.createSelectorQuery()
          .in(this)[ all ? 'selectAll' : 'select' ](selector)
          .boundingClientRect((rect) => {
            if (all && Array.isArray(rect) && rect.length) {
              resolve(rect)
            }
            if (!all && rect) {
              resolve(rect)
            }
          })
          .exec()
      })
    },
  },
}
</script>

<style lang="scss" scoped>
// scssmixin4css
// nvuedisplay:flex
@mixin flex($direction: row) {
  display: flex;
  flex-direction: $direction;
}

// uViewnvueflex-direction: column;
// nvueflex-direction: column; 
view,
scroll-view,
swiper-item {
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  flex-grow: 0;
  flex-basis: auto;
  align-items: stretch;
  align-content: flex-start;
}

.u-tabs {

  &__wrapper {
    @include flex;
    align-items: center;
    padding-bottom: v-bind(lineHeightH);

    &__scroll-view-wrapper {
      flex: 1;
      overflow: auto hidden;
    }

    &__scroll-view {
      @include flex;
      flex: 1;
    }

    &__nav {
      @include flex;
      position: relative;

      &__item {
        padding: 0 11px;
        @include flex;
        align-items: center;
        justify-content: center;

        &--disabled {
          cursor: not-allowed;
        }

        &__text {
          font-size: 15px;
          color: #606266;

          &--disabled {
            color: #c8c9cc !important;
          }
        }
      }

      &__line {
        height: 3px;
        background: #3c9cff;
        width: 30px;
        position: absolute;
        bottom: 2px;
        border-radius: 100px;
        transition-property: transform;
        transition-duration: 300ms;
      }
    }
  }
}

.u-tabs-content {}
</style>
