import { nextTick, ref } from 'vue'

/**
 * scroll-view
 * @returns
 */
export function useScrollTop() {
  const scrollTop = ref(0)
  let oldScrollTop = 0

  /**
   * 
   */
  const scroll = (e: ScrollEvent) => {
    oldScrollTop = e.detail.scrollTop
  }

  /**
   * 
   */
  const goTop = () => {
    scrollTop.value = oldScrollTop
    nextTick(() => {
      scrollTop.value = 0
    })
  }

  return {
    scrollTop,
    scroll,
    goTop
  }
}
