import { request } from './request'

/**
 * https://yapi.public.supaur.top/project/2947/interface/api/258938
 *
 * @param data object
 * @description cityCode 
 * @description pageNo 
 * @description pageSize 
 * @returns
 */
export const getDealerList: Dealer.GetDealer = (data) => {
  return request({
    url: '/web/dealers/',
    method: 'GET',
    domain: 'MALL',
    data
  })
}

