import { request } from './request'

/**
 * 
 * @returns
 */
export const getBusinessId = () => {
  return request({
    url: '/web/orders/business-id',
    method: 'GET',
    domain: 'MALL'
  })
}

/**
 * 
 * @param data
 * @returns
 */
export const getPayOrder: Order.PayOrder = (data) => {
  return request({
    url: `/web/orders/${data.orderId}/pay`,
    method: 'POST',
    domain: 'MALL',
    data
  })
}
