interface ConsumerPlacesAPI {
  brandId?: string | number // id
  pageNo?: number
  pageSize?: number
}
/**
 * https://yapi.public.supaur.top/project/2920/interface/api/247906
 * 
 * @param params
 * @returns
 */

export const getConsumerPlaces = (params: ConsumerPlacesAPI) => {
  return request({
    url: '/web/retail-stores',
    method: 'GET',
    data: params,
    domain: 'MALL'
  })
}

