// @ts-nocheck
function _now() {
  return +new Date()
}

export const debounce: DebounceFn<T | undefined> = function (func, wait = 300, immediate = false): Fn<T | undefined> {
  // immediatefalse
  var timeout, args, context, timestamp, result

  var later = function () {
    // wait_.debouncetimestamplast < wait && last >= 0truefunc
    var last = _now() - timestamp

    if (last < wait && last >= 0) {
      timeout = setTimeout(later, wait - last)
    } else {
      timeout = null
      if (!immediate) {
        result = func.apply(context, args)
        if (!timeout) context = args = null
      }
    }
  }

  return function () {
    context = this
    args = arguments
    timestamp = _now()
    // immediatetruefunc
    var callNow = immediate && !timeout
    // waitfunc
    if (!timeout) timeout = setTimeout(later, wait)
    if (callNow) {
      result = func.apply(context, args)
      context = args = null
    }
    return result
  }
}

export function throttle(func, wait = 300, options) {
  /* options
   *  funcwaitfunc
   *  options.leading = true;
   * waitfuncwaitfunc
   *  options.trailing = true;
   * options.trailing = false
   */
  let context, args, result
  var timeout = null
  var previous = 0
  if (!options) {
    options = {}
  }
  var later = function () {
    previous = options.leading === false ? 0 : _now()
    timeout = null
    result = func.apply(context, args)
    if (!timeout) {
      context = args = null
    }
  }
  return function () {
    var now = _now()
    if (!previous && options.leading === false) {
      previous = now
    }
    // 
    var remaining = wait - (now - previous)
    context = this
    args = arguments
    // waitfunc
    // remaining <= 0waitfunc
    if (remaining <= 0 || remaining > wait) {
      // setTimeoutwaitsetTimeoutsetTimeout
      if (timeout) {
        clearTimeout(timeout)
        timeout = null
      }
      previous = now
      result = func.apply(context, args)
      if (!timeout) {
        context = args = null
      }
    } else if (!timeout && options.trailing !== false) {
      // options.trailing=truefunc
      timeout = setTimeout(later, remaining)
    }
    return result
  }
}
